.modal {
  background: #213564b5;
  display: none;
  justify-content: center;
  perspective: 500px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: fixed;
  // opacity: 90%;
  align-items: center;
  z-index: 10;
}

.modal-wrapper {
  max-width: 950px;
  margin: 10px;
  width: 100%;
  background: #FFFFFF;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  z-index: 200;
}



.modal-header {
  margin-top: 6px;
  position: relative;
  display: flex;
    flex-direction: column;
    align-items: center;
}

.close-btn {
  opacity: 0.8;
  margin-left: 238px;
  margin-bottom: 10px;
  width: 24px;
  height: 24px;
  cursor: pointer;
  position: absolute;
  right: 10px;
  top: 0;
}

.modal-subtitle {
  font-weight: 400;
font-size: 32px;
line-height: 44px;
font-family: 'Roboto';
color: #213564;
text-align: center;
margin-top: 50px;
}


.modal-img {
  width: 70px;
  height: 70px;
  cursor: pointer;
  @media all and (max-width: 800px) {
    width: 55px;
  height: 55px;
  }
}
.modal-wrap {
  max-width: 810px;
  width: 100%;
  padding: 30px 10px 80px;
}

.modal-img:not(:nth-child(9n+9)) {
 margin-right: 16px;
 @media all and (max-width: 900px) {
  margin-right: 10px;
 }
 .modal-img:not(:nth-child(9n+9)) {
  margin-right: 0;
}
}

@media all and (max-width: 800px){
  .modal-img:not(:nth-child(10n+10)) {
    margin-right: 10px;
  }

}