.reset {
  display: flex;
  width: 230px;
  padding: 8px 16px;
  margin: 0 auto;
  margin-bottom: 10px;
  
  border: solid 1px #c7ccd1;
  border-radius: 5px;
  
  transition: all 0.2s ease-out;
  
  cursor: pointer;
  
  font-weight: bold;
  
  background-color: #ffffff;
  color: #333333;
  
  &:hover {
    background-color: #D8093A;
    color: #ffffff;
  }
}

.__select {
  position: relative;

  display: flex;
  width: 230px;
  height: 40px;
  margin: 0 auto;

  z-index: 1;
  
  &[data-state="active"] {
    .__select__title {
      &::before {
        transform: translate(-3px, -50%) rotate(-45deg);
      }

      &::after {
        transform: translate(3px, -50%) rotate(45deg);
      }
    }
    
    .__select__content {
      border-width: 1px;
      
      border-color: rgba(#c7ccd1, 1);
    }
    
    .__select__label {
      max-height: 40px;
      
      border-width: 1px;
      
      &::before {
        opacity: 1;
      }
    }
  }
}

.__select__title {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 8px 16px;

  border-radius: 5px;
  border: solid 1px #c7ccd1;
  
  cursor: pointer;
  
  z-index: 2;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 50%;
    right: 16px;

    display: block;
    width: 10px;
    height: 2px;

    transition: all 0.3s ease-out;

    background-color: #333333;

    transform: translate(-3px, -50%) rotate(45deg);
  }
  &::after {
    transform: translate(3px, -50%) rotate(-45deg);
  }

  &:hover {
    border-color: #64AFE2;

    &::before,
    &::after {
      background-color: #64AFE2;
    }
  }
}

.__select__content {
  position: absolute;
  top: 40px;
  left: 3px;

  display: flex;
  flex-direction: column;
  width: calc(100% - 6px);

  background-color: #ffffff;

  border: 1px solid rgba(#c7ccd1, 0);
  border-top-width: 0;
  border-bottom-width: 0;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  
  transition: all 0.3s ease-out;

  z-index: 1;
}
.__select__input {
  display: none;
  pointer-events: none;

  &:checked + label {
    background-color: #dedede;
    
    &::after {
      content: attr(data-value);
    }
  }
  &:disabled + label {
    opacity: 0.6;
    pointer-events: none;
  }
}
.__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  max-height: 0;
  padding: 0 16px;
  
  transition: all 0.2s ease-out;

  cursor: pointer;

  & + input + & {
    border-top: 0 solid #C7CCD160;
  }

  &:hover {
    background-color: #64AFE2 !important;

    color: #ffffff;
  }

  &::before {
    content: attr(data-value);
    
    transition: all 0.2s ease-out;
    
    opacity: 0;
  }
  &::after {
    position: absolute;
    top: -40px;
    left: 0;
    
    display: flex;
    align-items: center;
    width: 200px;
    height: 40px;
    padding: 8px 16px;
    
    color: #62baea;
    
    z-index: -1;
  }
}

.__select__title {
  border-radius: 1px;
  -webkit-border-radius: 1px;
  -moz-border-radius: 1px;
  -ms-border-radius: 1px;
  -o-border-radius: 1px;
}


.select-image {
  position: relative;
  background-color: #ffffff;
}
.select-image .select-image-input {
  border: 1px solid #CBCBCB;
  display: flex;
  align-items: center;
  cursor: pointer;
  width: 260px;
  height: 50px;
  font-family: 'Arial';
  font-weight: 400;
  font-size: 16px;
  line-height: 175%;
}
.select-image .select-image-list {
  display: none;
  background-color: #ffffff;
  position: absolute;
  z-index: 2;
  width: 100%;
}
.select-image .select-image-list.show {
  display: block;
}
.select-image .select-image-list .select-image-option {
  display: flex;
  align-items: center;
  cursor: pointer;
  height: 50px;
}
