.select-link {
  font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 175%;
color: #3C9BE0;
text-align: right;
margin-right: 20px;
}
.select-img_wrap {
  display: flex;
  align-items: center;
}

.dropdown input,
.dropdown .options {
  border-radius: 1px;
  -webkit-border-radius: 1px;
  -moz-border-radius: 1px;
  -ms-border-radius: 1px;
  -o-border-radius: 1px;
}