.calculator {
  padding: 0 20px;
  max-width: 990px;
  width: 100%;
  margin: 0 auto;
  margin-top: 123px;
  @media all and (max-width: 1020px) {
    margin-top: 80px;
  }
  @media all and (max-width: 420px) {
    margin-top: 40px;
    padding: 0 10px;
  }
  &_title {
    font-family: "Roboto";
    font-weight: 400;
    color: #213564;
    font-size: 40px;
    line-height: 44px;
    text-align: center;
    @media all and (max-width: 820px) {
      margin-top: 36px;
    }
    @media all and (max-width: 420px) {
      font-size: 26px;
      line-height: 26px;
    }
  }
  &_group-button {
    display: flex;
    justify-content: space-between;
    margin: 40px 0 65px;
    flex-wrap: wrap;
    align-items: center;
    @media all and (max-width: 1020px) {
      justify-content: center;
    }
    @media all and (max-width: 620px) {
      margin: 24px 0 30px;
    }
    @media all and (max-width: 500px) {
      display: none;
    }
  }
  &_button {
    width: 303px;
    height: 55px;
    background: #EDEDED;
    Color: #213564;
    font-family: "Roboto";
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    border: none;
    &:focus, &:hover {
      background: #213564;
      color: #ffffff;
    }
    @media all and (max-width: 1020px) {
      margin: 6px;
    }
  }
  &_subtitle {
    font-family: "Roboto";
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    margin-bottom: 20px;
  }
  &_select,
  &_option {
    width: 260px;
    height: 50px;
    background: #FFFFFF;
    border: 1px solid #CBCBCB;
    font-family: "Arial";
    padding: 0 20px;
  }
  &_price {
    background: #213564;
    color: #ffffff;
    max-width: 390px;
    width: 100%;
    padding: 66px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    @media all and (max-width: 990px) {
      height: max-content;
    }
    @media all and (max-width: 860px) {
      display: none;
    }
  }
  &_price_wrap {
    display: flex;
    align-items: center;
    margin-bottom: 32px;
  }
  &_price_text{
    font-weight: 400;
    font-size: 22px;
    line-height: 32px;
    margin-right: 10px;
  }
  &_price_sum {
    color: #F5BF39;
    font-weight: 400;
    font-size: 36px;
    line-height: 44px;
  }
  &_price_sum:last-child {
    margin-left: 10px;
  }
  &_price_input,
  &_price_input::placeholder {
    background: #FFFFFF;
    width: 230px;
    height: 50px;
    padding-left: 20px;
    font-family: 'Arial';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
  }
  &_price_input {
    margin-bottom: 16px;
    border: 1px solid #CBCBCB;
  }
  &_price_btn {
    background: #F5BF39;
    border-radius: 10px;
    width: 230px;
    height: 65px;
    font-family: 'Arial';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
  }
  &_blocks, &_block, &_progress_wrapper {
    display: flex;
  }
  &_progress_subtitle {
    font-family: 'Arial';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
  }
  &_progress_wrap {
    margin-right: 5px;
  }
  &_progress_wrapper {
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
    
  }
  &_block {
    flex-wrap: wrap;
    margin-right: 30px;
    justify-content: space-between;
    @media all and (max-width: 640px) {
      margin-right: 0;
      display: grid;
    }
  }
  &_select-wrap {
    max-width: 268px;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  &_select-wrap:nth-child(3), &_select-wrap:nth-child(4), &_progress_wrapper {
    margin-top: 32px;
  }

  &_select-wrap-bottom {
    @media all and (max-width: 590px) {
      margin-top: 16px;
    }
  }
  
  &_images {
    padding-right: 3px;
  }
  &_paintings {
    display: flex;
    justify-content: space-between;
  }
  &_paintings:last-child {
    margin-top: 25px;
  }

  &_img:hover {
    fill: #213564;
  }
  &_options_select {
    width: 140px;
    height: 50px;
    padding: 0 0 0 16px;
  }
  &_number_input {
    width: 78px;
    height: 40px;
    padding: 12px;
  }
  &_options {
    margin-top: 35px;
  }
  &_options_wrapper {
    background: #EDEDED;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 550px;
    width: 100%;
  }
  &_options_wrap {
    display: flex;
    position: relative  !important;
  }
  &_number-height {
    position: absolute;
    left: -126%;
    margin-top: 32px;
  }
  &_number:last-child {
    margin: 20px 0 68px;
  }
  &_number_text {
    margin-bottom: 8px;
  }
  &_wrapping {
    display: flex;
    justify-content: space-between;
    margin: 30px 0;
    @media all and (max-width: 590px) {
      flex-direction: column;
    }
  }
  &_wrap-left {
    max-width: 550px;
    width: 100%;
  }
  &_input {
    font-family: 'Arial';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
  }
  &_form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 30px;
    grid-row-gap: 14px;
    margin-bottom: 40px;
    @media all and (max-width: 470px) {
      grid-template-columns: repeat(1, 1fr);
    }
    
  }
  &_wrap_form {
    background: #EDEDED;
  }
  &_options_wrap {
    position: relative;
  }
  
}

.dropdown-top input, .options-top{
  width: 335px !important;
  background: #EDEDED !important;
  color:#213564

}
.dropdown-top {
  width: 337px !important;
  display: none;
  @media all and (max-width: 500px) {
    display: block;
    margin: 0 auto !important;
    padding: 28px 0 20px !important;
  }

}
.dropdown-top::placeholder {
  color: #213564;
  font-family: "Roboto";
}
.text-box-top {
  font-family: "Roboto";
}
.dropdown-top_wrap {
  margin: 28px 0 20px;
}


.select-text {
  margin-right: 48px;
}

.select-text:first-child {
  margin-right: 34px;
}
.alculator_options_wrap {
  position: relative;
}